"use client"

import { useState, useRef } from "react"
import { Button } from "@/components/ui/button"

export default function CameraTest() {
    const [isActive, setIsActive] = useState(false)
    const videoRef = useRef<HTMLVideoElement>(null)
    const streamRef = useRef<MediaStream | null>(null)

    const startCamera = async () => {
        try {
            console.log('üé• Requesting camera...')
            const stream = await navigator.mediaDevices.getUserMedia({
                video: { facingMode: "user", width: 640, height: 480 },
                audio: false
            })

            console.log('‚úÖ Camera stream obtained:', stream)

            if (videoRef.current) {
                videoRef.current.srcObject = stream
                streamRef.current = stream
                setIsActive(true)
                console.log('‚úÖ Video element updated')
            }
        } catch (error) {
            console.error('‚ùå Camera error:', error)
            alert(`Ïπ¥Î©îÎùº Ïò§Î•ò: ${error}`)
        }
    }

    const stopCamera = () => {
        if (streamRef.current) {
            streamRef.current.getTracks().forEach(track => track.stop())
            streamRef.current = null
        }
        if (videoRef.current) {
            videoRef.current.srcObject = null
        }
        setIsActive(false)
    }

    return (
        <div className="min-h-screen flex items-center justify-center bg-gray-50 p-8">
            <div className="max-w-2xl w-full space-y-4">
                <h1 className="text-2xl font-bold">Ïπ¥Î©îÎùº ÌÖåÏä§Ìä∏</h1>

                <div className="relative aspect-video bg-black rounded-lg overflow-hidden">
                    <video
                        ref={videoRef}
                        autoPlay
                        playsInline
                        muted
                        className="w-full h-full object-cover"
                    />
                </div>

                <div className="flex gap-4">
                    {!isActive ? (
                        <Button onClick={startCamera}>Ïπ¥Î©îÎùº ÏãúÏûë</Button>
                    ) : (
                        <Button onClick={stopCamera} variant="destructive">Ïπ¥Î©îÎùº Ï§ëÏßÄ</Button>
                    )}
                </div>

                <div className="text-sm text-gray-600">
                    <p>F12Î•º ÎàåÎü¨ ÏΩòÏÜîÏùÑ ÌôïÏù∏ÌïòÏÑ∏Ïöî</p>
                </div>
            </div>
        </div>
    )
}
